.Toolbar {
	height: 60px;
	width: 100%;
	position: fixed;
	top: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: start;
	    justify-content: flex-start; 
	-ms-flex-align: center; 
	    align-items: center;
	padding: 0 20px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	z-index: 90;
}

header nav {
	height: 100%;
	margin-left: auto;
}

header div {
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

@media (max-width: 499px) {
	.DesktopOnly {
		display: none;
	}
}

@media (min-width: 499px) {
	div.hamburgerMenu {
		display: none;
	}
}


